body{
  font-size: 12px;
  font-family: Bellota;
}
.mainBox{
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;

}

.greetScreen{
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logoBox{
  margin-top: 100px;
  position: relative;
}

.kodi{
  position:absolute;
  left:224px;
  top:-60px;
  transform: rotate(-30deg);

}

.logo{
  width: 600px;
  position: relative;
}

.battleBox{
  display: flex;
  justify-content: space-between;
  margin: 40px;

}

.pokemonBox{
  width: 200px;
  display: flex;
  flex-direction: column;
  margin: 10px;
}

.progresBar{
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  height: 10px;
  border: 1px solid #079325;
  border-radius: 5px;
}
.progres{
  width: 100px;
  height: 10px;
  background-color: #62FF84;
}

.percent{
  text-align: center;
}

.pokemonImg{
  width: 200px;
}

#img2{
   -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.name{
  text-align: center;
  text-transform: capitalize;
}

.stats{
  font-size: 12px;
  padding: 6px;
  margin-top: 6px;
}

.menu{
  padding: 5px;
  width: 170px;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 6px;
  margin-top: 6px; 
}

.okvir{
 background-color: #FFF7D6;
  border: 3px solid #FFCC00;
  border-radius: 10px;
}

.logs {
width: 400px;
min-height: 150px;
max-height: 250px;
padding: 5px;
text-transform:capitalize;
overflow:auto;
}
.logs p{
  margin: 4px;
}

.menu button{
  margin: 4px;
}

.down{
  display: flex;
  justify-content: space-around;
}

.message{
  color: red;
  font-size: 20px;
  z-index: 10;
}

.pobjednik{
  position: absolute;
  top: 50px;
  left: 40%;
}

.attackBox{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.attackBox img{
  margin: 10px;
}

button{
  width: 160px;
  color: white;
  background-color: #0073BC;
  border: 3px solid #73B9E5;
  padding: 8px;
  border-radius: 20px;
  
}


.animate1 {
  animation: attack1 700ms ease-in;
}

@keyframes attack1 {
  0%   {transform: translateX(0px);}
  50%  {transform: translateX(600px);}
  100% {transform: translateX(0px);}
}

.animate2 {
  animation: attack2 700ms ease-in;
}

@keyframes attack2 {
  0%   {transform: translateX(0px);}
  50%  {transform: translateX(-600px);}
  100% {transform: translateX(0px);}
}

.animateMiss {
  animation: miss 700ms ease-in;
}

@keyframes miss {
  0%   {transform: rotate(0deg);}
  50%  {transform: rotate(180deg);}
  100% {transform: rotate(359deg);}
}

@media screen and (max-width: 700px) {
  .down{
    display: flex;
    flex-direction: column;
  }
  .battleBox button{
    width: 90px;
  }
  .battleBox{
  margin: 0px;
  }
  .pokemonBox{
    width: 40%;
    margin: 0px;
  }
  .logo{
    width: 90%;
  }
  .kodi{
    width: 22%;
    left:32%;
    top:-28%;
  }
  .greetScreen{
    width: 100vw;
  }
}